<template>
    <v-app>
        <!-- -->
        <v-img
        v-if="imageUrl" :src="imageUrl"
        max-height="200"
        max-width="100%"
        ></v-img>
        <!-- -->
        <v-img
        v-else
        max-height="200"
        max-width="100%"
        src="..\assets\banner.jpg"
        ></v-img>
    <br>
    <v-container style="width: 60%;">
    <v-layout column wrap>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center"> </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-text-field
                    label=" "
                    :rules="rules"
                    counter="100"
                    outlined
                    ></v-text-field>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center"> tag</p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-autocomplete
                    :items="items"
                    outlined
                    dense
                    chips
                    small-chips
                    label=""
                    multiple
                    ></v-autocomplete>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center"> </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-textarea
                    counter="1000"
                    outlined
                    label=" "
                    :rules="area_rules"
                    ></v-textarea>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">  </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <input ref="imageInput" type="file" hidden @change="onChangeImages">
                    <v-btn type="button" @click="onClickImageUpload"> </v-btn>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">  </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-radio-group
                    v-model="profile_radios"
                    :mandatory="false"
                    >
                        <v-radio
                        label="My "
                        value="radio-1"
                        ></v-radio>
                        <v-radio
                        label=" "
                        value="radio-2"
                        ></v-radio>
                    </v-radio-group>
                    <v-text-field
                    label=""
                    v-model="my.name"
                    :disabled="profile_radios == 'radio-1'"
                    outlined
                    ></v-text-field>
                    <v-autocomplete
                    v-model="my.tag"
                    :items="items"
                    :disabled="profile_radios == 'radio-1'"
                    outlined
                    dense
                    chips
                    small-chips
                    label=""
                    multiple
                    ></v-autocomplete>
                    <v-textarea
                    label=""
                    v-model="my.career"
                    :disabled="profile_radios == 'radio-1'"
                    outlined
                    ></v-textarea>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">  </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-radio-group
                    v-model="num_radios"
                    :mandatory="false"
                    >
                        <v-radio
                        label=""
                        value="radio-1"
                        ></v-radio>
                        <v-radio
                        label=""
                        value="radio-2"
                        ></v-radio>
                    </v-radio-group>
                    <v-select
                    :items="member_num"
                    v-show="num_radios == 'radio-2'"
                    label=" "
                    dense
                    outlined
                    ></v-select>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">   </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-radio-group
                    v-model="funding_radios"
                    :mandatory="false"
                    >
                        <v-radio
                        label="  "
                        value="radio-1"
                        ></v-radio>
                        <v-radio
                        label=" "
                        value="radio-2"
                        ></v-radio>
                    </v-radio-group>
                    <v-text-field
                    label="  "
                    suffix="point"
                    v-show="funding_radios == 'radio-2'"
                    value="0"
                    style="width:35%"
                    ></v-text-field>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">   </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-radio-group
                    v-model="deadline_radios"
                    :mandatory="false"
                    >
                        <v-radio
                        label=""
                        value="radio-1"
                        ></v-radio>
                        <v-radio
                        label=" "
                        value="radio-2"
                        ></v-radio>
                    </v-radio-group>
                    <div v-show="deadline_radios == 'radio-2'">
                    <Calendar></Calendar>
                    </div>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <div class="text-center">
            <v-btn
            v-model="fab"
            >
             </v-btn>
            </div>
        </v-flex>
    </v-layout>
    </v-container>
    </v-app>
</template>

<script>
import Calendar from '../components/calendar'
export default {
    name: 'projectStart',
    components: {
        Calendar
    },

    data() {
        return{
            items: ['', '', '', 'IT', '','','','',','],
            rules: [v => v.length <= 100 || 'Max 100 characters'],
            area_rules: [v => v.length <= 1000 || 'Max 1000 characters'],
            member_num: ['2','3','4','5','6','7','8','9','10'],
            num_radios: 'radio-1',
            profile_radios: 'radio-1',
            deadline_radios: 'radio-1',
            funding_radios: 'radio-1',
            my: {
                name: "",
                tag: ['', ''],
                career: '  '
            },
            imageUrl: null,
        }
    },
    methods: {
        onClickImageUpload() {
            this.$refs.imageInput.click();
        },
        onChangeImages(e) {
            console.log(e.target.files)
            const file = e.target.files[0];
            this.imageUrl = URL.createObjectURL(file);
        }
    },

}
</script>

        